/* Status Modal Styles */
.status-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  animation: fadeIn 0.3s ease-out;
  padding: 20px;
}

.status-modal {
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  max-width: 480px;
  width: 100%;
  max-height: 90vh;
  overflow: auto;
  position: relative;
  animation: slideUp 0.3s ease-out;
  transform-origin: center bottom;
}

.status-modal__header {
  position: relative;
  padding: 32px 32px 0 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.status-modal__icon {
  font-size: 64px;
  margin-bottom: 16px;
  animation: bounceIn 0.6s ease-out;
}

.status-modal__close {
  position: absolute;
  top: 16px;
  right: 16px;
  background: #f3f4f6;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #6b7280;
  transition: all 0.2s ease;
  font-size: 14px;
}

.status-modal__close:hover {
  background: #e5e7eb;
  color: #374151;
  transform: scale(1.1);
}

.status-modal__body {
  padding: 0 32px 32px 32px;
  text-align: center;
}

.status-modal__title {
  font-size: 24px;
  font-weight: 700;
  margin: 0 0 12px 0;
  color: #1f2937;
}

.status-modal__message {
  font-size: 16px;
  color: #6b7280;
  line-height: 1.5;
  margin: 0;
}

.status-modal__footer {
  padding: 0 32px 32px 32px;
  display: flex;
  justify-content: center;
}

.status-modal__button {
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 32px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 120px;
}

.status-modal__button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(59, 130, 246, 0.4);
}

.status-modal__button:active {
  transform: translateY(0);
}

.status-modal__progress {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: #f3f4f6;
  border-radius: 0 0 16px 16px;
  overflow: hidden;
}

.status-modal__progress-bar {
  height: 100%;
  background: linear-gradient(90deg, #3b82f6, #1d4ed8);
  width: 100%;
  transform: translateX(-100%);
  animation: progressBar linear;
}

/* Modal Variants */
.status-modal--success .status-modal__button {
  background: linear-gradient(135deg, #10b981, #059669);
}

.status-modal--success .status-modal__button:hover {
  box-shadow: 0 10px 25px rgba(16, 185, 129, 0.4);
}

.status-modal--success .status-modal__progress-bar {
  background: linear-gradient(90deg, #10b981, #059669);
}

.status-modal--error .status-modal__button {
  background: linear-gradient(135deg, #ef4444, #dc2626);
}

.status-modal--error .status-modal__button:hover {
  box-shadow: 0 10px 25px rgba(239, 68, 68, 0.4);
}

.status-modal--error .status-modal__progress-bar {
  background: linear-gradient(90deg, #ef4444, #dc2626);
}

.status-modal--loading .status-modal__close {
  display: none;
}

.status-modal--loading .status-modal__icon {
  color: #3b82f6;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(100px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes progressBar {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.spinning {
  animation: spin 1s linear infinite;
}

/* Mobile Responsive */
@media (max-width: 640px) {
  .status-modal-overlay {
    padding: 16px;
  }

  .status-modal {
    max-width: 100%;
    margin: 0;
  }

  .status-modal__header {
    padding: 24px 24px 0 24px;
  }

  .status-modal__icon {
    font-size: 48px;
    margin-bottom: 12px;
  }

  .status-modal__body {
    padding: 0 24px 24px 24px;
  }

  .status-modal__title {
    font-size: 20px;
  }

  .status-modal__message {
    font-size: 14px;
  }

  .status-modal__footer {
    padding: 0 24px 24px 24px;
  }

  .status-modal__button {
    width: 100%;
    padding: 14px 24px;
  }

  .status-modal__close {
    top: 12px;
    right: 12px;
    width: 28px;
    height: 28px;
    font-size: 12px;
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .status-modal {
    background: #1f2937;
    color: #f9fafb;
  }

  .status-modal__title {
    color: #f9fafb;
  }

  .status-modal__message {
    color: #d1d5db;
  }

  .status-modal__close {
    background: #374151;
    color: #d1d5db;
  }

  .status-modal__close:hover {
    background: #4b5563;
    color: #f9fafb;
  }

  .status-modal__progress {
    background: #374151;
  }
}

/* High contrast accessibility */
@media (prefers-contrast: high) {
  .status-modal {
    border: 2px solid #000;
  }

  .status-modal__button {
    border: 2px solid transparent;
  }

  .status-modal__button:focus {
    outline: 3px solid #000;
    outline-offset: 2px;
  }
}

/* Reduced motion accessibility */
@media (prefers-reduced-motion: reduce) {
  .status-modal-overlay,
  .status-modal,
  .status-modal__icon,
  .status-modal__button,
  .status-modal__progress-bar,
  .spinning {
    animation: none;
  }

  .status-modal__button:hover {
    transform: none;
  }
}

/* Focus management */
.status-modal__button:focus,
.status-modal__close:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Print styles */
@media print {
  .status-modal-overlay {
    display: none;
  }
}
