/* Sidebar List */
.sidebar-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Sidebar Parent Item */
.sidebar-list__item {
  margin-bottom: 4px;
}

/* Sidebar Parent Link */
.sidebar-list__link {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  color: #333;
  font-weight: 500;
  text-decoration: none;
  transition: background 0.2s;
}

.sidebar-list__link:hover {
  background-color: #f0f0f0;
}

/* Icon Wrapper */
.sidebar-list__icon {
  position: relative;
  width: 24px;
  height: 24px;
  margin-right: 12px;
}

.sidebar-list__icon img {
  position: absolute;
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
  transition: opacity 0.3s;
}

.sidebar-list__icon .icon {
  opacity: 1;
}

.sidebar-list__icon .icon-active {
  opacity: 0;
}

/* When parent is active */
.sidebar-list__link.activePage .icon {
  opacity: 0;
}

.sidebar-list__link.activePage .icon-active {
  opacity: 1;
}

/* Submenu Arrow */
.submenu-arrow {
  margin-left: auto;
  font-size: 12px;
  color: #888;
}

/* Submenu List */
.sidebar-list .sidebar-submenu__item {
  padding-left: 48px;
}

.sidebar-submenu__link {
  display: block;
  padding: 10px 16px;
  color: #555;
  text-decoration: none;
  font-size: 14px;
  transition: background 0.2s;
}

.sidebar-submenu__link:hover {
  background-color: #f9f9f9;
}

.sidebar-submenu__link.activePage {
  background-color: #e6f2ff;
  color: #007bff;
  font-weight: 600;
}


